<script setup>
import { Handle, Position } from "@vue-flow/core";
import Multiselect from "vue-multiselect";
</script>
<script>
export default {
  name: "KnowledgeBaseNode",
  props: {
    id: String,
    isSelected: Boolean,
    data: Object,
    knowledgeBase: Object,
  },
  components: { Multiselect },
  data() {
    return {
      label: this.data.label,
      nodeData: this.data,
      KnowledgeBaseList: this.knowledgeBase,
      isFold: true,
      isFold_inputs: true,
      isFold_outputs: true,
      isFold_KnowledgeBase: true,
    };
  },
  methods: {
    saveNode() {
      this.$emit("saveNode", {
        id: this.id,
        data: { ...this.nodeData, label: this.label },
      });
    },
    saveLabel() {
      this.saveNode();
    },
    checkFold() {
      this.isFold = !this.isFold;
    },
    checkFoldInputs() {
      this.isFold_inputs = !this.isFold_inputs;
    },
    checkFoldOutputs() {
      this.isFold_outputs = !this.isFold_outputs;
    },
    checkFoldKnowledgeBase() {
      console.log(this.KnowledgeBaseList);
      this.isFold_KnowledgeBase = !this.isFold_KnowledgeBase;
    },
  },
};
</script>
<style src="vue-multiselect/dist/vue-multiselect.css"></style>
<template>
  <Handle class="Handle" type="target" :position="Position.Left" :style="{}" />
  <Handle class="Handle" type="source" :position="Position.Right" :style="{}" />
  <div
    class="NodeBox KnowledgeBaseNode iconfont"
    :class="[isSelected ? 'Selected' : '', '']"
  >
    <div class="Title">
      <span
        class="Icon fold"
        :class="[isFold ? 'icon-jiantou-d' : 'icon-jiantou-r']"
        @click="checkFold"
      ></span>
      <span class="Icon ticon icon-KnowledgeBase"></span>
      <span>知识库</span>
      <input
        class="label"
        v-model="label"
        value="{{label}}"
        placeholder="备注"
        @blur="saveLabel"
      />
    </div>
    <div class="Content" v-if="isFold">
      <div class="Description">根据输入的文本，调用知识库，获取知识库数据</div>

      <div class="Inputs Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_inputs ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldInputs"
          ></span>
          <span>输入</span>
        </div>
        <div v-if="isFold_inputs">
          <table>
            <thead>
              <th>变量名</th>
              <th>描述</th>
            </thead>
            <tbody>
              <tr v-for="(item, i) in nodeData.inputs" :key="i">
                <td><input v-model="item.varname" /></td>
                <td><input v-model="item.txt" /></td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>

      <div class="KnowledgeBase Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_KnowledgeBase ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldKnowledgeBase"
          ></span>
          <span>知识库</span>
        </div>
        <div v-if="isFold_KnowledgeBase">
          <multiselect
            v-model="nodeData.knowledgeBase"
            placeholder="请选择知识库"
            :multiple="true"
            :close-on-select="false"
            :clear-on-select="false"
            :preserve-search="true"
            label="Name"
            track-by="Name"
            taggable="true"
            :options="KnowledgeBaseList"
          ></multiselect>
        </div>
      </div>

      <div class="Outputs Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_outputs ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldOutputs"
          ></span>
          <span>输出</span>
        </div>
        <div v-if="isFold_outputs">
          <table>
            <thead>
              <th>变量名</th>
              <th>描述</th>
            </thead>
            <tbody>
              <tr v-for="(item, i) in nodeData.output" :key="i">
                <td><input v-model="item.varname" /></td>
                <td><input v-model="item.txt" /></td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>
