<script setup>
import { Handle, Position } from "@vue-flow/core";
</script>
<script>
export default {
  name: "PluginsNode",
  props: {
    id: String,
    isSelected: Boolean,
    data: Object,
    plugins: Object,
  },
  data() {
    return {
      label: this.data.label,
      nodeData: this.data,
      pluginsList: this.plugins,
      isFold: true,
      isFold_inputs: true,
      isFold_outputs: true,
      isFold_plugins: true,
    };
  },
  methods: {
    saveNode() {
      this.$emit("saveNode", {
        id: this.id,
        data: { ...this.nodeData, label: this.label },
      });
    },
    saveLabel() {
      this.saveNode();
    },
    checkFold() {
      this.isFold = !this.isFold;
    },
    checkFoldInputs() {
      this.isFold_inputs = !this.isFold_inputs;
    },
    checkFoldOutputs() {
      this.isFold_outputs = !this.isFold_outputs;
    },
    checkFoldPlugins() {
      this.isFold_plugins = !this.isFold_plugins;
    },
  },
};
</script>
<template>
  <Handle class="Handle" type="target" :position="Position.Left" :style="{}" />
  <Handle class="Handle" type="source" :position="Position.Right" :style="{}" />
  <div class="NodeBox Plugins iconfont" :class="[isSelected ? 'Selected' : '', '']">
    <div class="Title">
      <span
        class="Icon fold"
        :class="[isFold ? 'icon-jiantou-d' : 'icon-jiantou-r']"
        @click="checkFold"
      ></span>
      <span class="Icon ticon icon-Plugins"></span>
      <span>插件</span>
      <input
        class="label"
        v-model="label"
        value="{{label}}"
        placeholder="备注"
        @blur="saveLabel"
      />
    </div>
    <div class="Content" v-if="isFold">
      <div class="Description">插件可以提供更多能力，外部操作，获取外部数据等</div>
      <div class="Plugins Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_plugins ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldPlugins"
          ></span>
          <span>插件</span>
        </div>
        <div v-if="isFold_plugins">
          <select v-model="nodeData.plugins.PluginsID">
            <option v-for="(item, i) in pluginsList" :key="i" :value="item.PluginsID">
              {{ item.Name }}
            </option>
          </select>
        </div>
      </div>
      <div class="Inputs Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_inputs ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldInputs"
          ></span>
          <span>输入</span>
        </div>
        <div v-if="isFold_inputs">
          <table>
            <thead>
              <th>变量名</th>
              <th>描述</th>
            </thead>
            <tbody>
              <tr v-for="(item, i) in nodeData.inputs" :key="i">
                <td><input v-model="item.varname" /></td>
                <td><input v-model="item.txt" /></td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>

      <div class="Outputs Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_outputs ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldOutputs"
          ></span>
          <span>输出</span>
        </div>
        <div v-if="isFold_outputs">
          <table>
            <thead>
              <th>变量名</th>
              <th>描述</th>
            </thead>
            <tbody>
              <tr v-for="(item, i) in nodeData.output" :key="i">
                <td><input v-model="item.varname" /></td>
                <td><input v-model="item.txt" /></td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>
