<script>
import { ref } from "vue";
import { VueFlow, useVueFlow, MarkerType } from "@vue-flow/core";
import { Background } from "@vue-flow/background";
import { ControlButton, Controls } from "@vue-flow/controls";
import { MiniMap } from "@vue-flow/minimap";
import //initialEdges,
//initialNodes,
//Plugins,
//LargeModel,
//KnowledgeBase,
"../initial-elements.js";
import Api from "../common/API.js";
import Tools from "../common/Tools.js";
import ToolIcon from "./ToolIcon.vue";
import ToolBox from "./ToolBox.vue";
import WorkFlowData from "./WorkFlowData.vue";
import LargeModelNode from "./Node/LargeModelNode.vue";
import PluginsNode from "./Node/PluginsNode.vue";
import StartNode from "./Node/StartNode.vue";
import EndNode from "./Node/EndNode.vue";
import SelectorNode from "./Node/SelectorNode.vue";
import KnowledgeBaseNode from "./Node/KnowledgeBaseNode.vue";
import CustomEdge from "./Edge/CustomEdge.vue";
import { notify } from "@kyvg/vue3-notification";

var workflow = null;

export default {
  name: "WorkFlow",
  props: {
    workflow_data: {
      title: "智能助理",
      WorkFlowID: "",
      MainID: "",
      MainType: 2,
    },
  },
  components: {
    VueFlow,
    Background,
    MiniMap,
    ToolBox,
    Controls,
    ToolIcon,
    ControlButton,
    WorkFlowData,
    LargeModelNode,
    PluginsNode,
    StartNode,
    EndNode,
    SelectorNode,
    CustomEdge,
    KnowledgeBaseNode,
  },
  setup() {
    const {
      onInit,
      onNodeDragStop,
      onConnect,
      addEdges,
      setViewport,
      toObject,
      onNodesChange,
      applyNodeChanges,
      onEdgesChange,
      applyEdgeChanges,
    } = useVueFlow();

    onInit((vueFlowInstance) => {
      vueFlowInstance.fitView();
    });
    onNodeDragStop(() => {
      workflow.nodes = toObject().nodes;
    });

    onConnect((connection) => {
      connection.id = Tools.guid();
      connection.markerEnd = MarkerType.ArrowClosed;

      connection.style = "stroke-width: 3;stroke:#3e47de;";
      addEdges(connection);
      workflow.edges = toObject().edges;
    });

    onNodesChange((changes) => {
      const nextChanges = [];
      for (const change of changes) {
        if (change.type === "remove") {
          var node = workflow.nodes.filter((item) => item.id === change.id);
          if (node.length > 0) {
            if (node[0].type === "Start") {
              notify({ type: "warn", title: "开始节点不能删除" });
              continue;
            } else if (node[0].type === "End") {
              notify({ type: "warn", title: "结束节点不能删除" });
              continue;
            } else {
              nextChanges.push(change);
            }
          }
        } else {
          nextChanges.push(change);
        }
      }
      applyNodeChanges(nextChanges);
    });

    onEdgesChange((changes) => {
      const nextChanges = [];
      for (const change of changes) {
        if (change.type === "remove") {
          if (workflow.selectedNode) {
            continue;
          } else {
            nextChanges.push(change);
          }
        } else {
          nextChanges.push(change);
        }
      }
      applyEdgeChanges(nextChanges);
    });

    const _setViewport = ref(setViewport);
    const _toObject = ref(toObject);
    return { _setViewport, _toObject };
  },
  mounted() {
    let _this = this;
    this.init();
    //this.startInterval();
    workflow = _this;
  },
  unmount() {
    this.stopInterval();
  },
  data() {
    return {
      ShowNodeBox: false,
      workflow: { title: this.workflow_data.title },
      pluginsList: [], //ref(Plugins),
      largeModelList: [], // ref(LargeModel),
      knowledgeBaseList: [], // ref(KnowledgeBase),
      nodes: [], // ref(initialNodes),
      edges: [], // ref(initialEdges),
      dark: ref(false),
      selectedNode: null,
      selectEdge: null,
      connectionLineStyle: {
        stroke: "#ddd",
        strokeWidth: 3,
      },

      intervalId: null,
    };
  },
  methods: {
    logToObject() {
      this.save();
      console.log(workflow.nodes);
      console.log(workflow.edges);
    },
    resetTransform() {
      this._setViewport({ x: 0, y: 0, zoom: 1 });
    },
    onShowNodeBox() {
      this.ShowNodeBox = !this.ShowNodeBox;
    },
    toggleDarkMode() {
      this.dark = !this.dark;
    },
    addNode(nodeObj) {
      console.log(nodeObj);
      this.nodes.push(nodeObj);
    },
    saveNode(nodeObj) {
      this.nodes = this.nodes.map((item) => {
        if (item.id == nodeObj.id) {
          item.data = nodeObj.data;
        }
        return item;
      });
    },
    nodeClick(node) {
      this.selectedNode = node;
      this.selectEdge = null;
    },
    edgeClick(edge) {
      this.selectedNode = null;
      this.selectEdge = edge;
    },
    startInterval() {
      this.intervalId = setInterval(this.save, 5000);
    },
    stopInterval() {
      if (this.intervalId) {
        clearInterval(this.intervalId);
      }
    },
    async init() {
      Api.getBaseConfig()
        .then((res) => {
          console.log(res);
          if (res.Status) {
            var Data = res.Data;
            this.largeModelList = ref(Data.largeModelList);
            this.pluginsList = Data.pluginsList;
            this.knowledgeBaseList = Data.knowledgeBaseList;
          }
          this.getWorkFlow();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getWorkFlow() {
      var that = this;
      Api.getWorkFlow(
        that.workflow_data.WorkFlowID,
        that.workflow_data.MainID,
        that.workflow_data.MainType
      )
        .then((res) => {
          console.log(res);
          if (res.Status) {
            var Data = res.Data;
            that.workflow_data.title = Data.Info.WorkflowName;
            Data.Nodes.map((x) => {
              this.nodes.push(x.Config);
            });
            Data.Edges.map((x) => {
              this.edges.push(x.Config);
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    save() {
      var that = this;

      var _WorkFlowID = that.workflow_data.WorkFlowID;
      var _Info = {
        WorkFlowID: _WorkFlowID,
        MainType: that.workflow_data.MainType,
        MainID: that.workflow_data.MainID,
        WorkflowName: "",
        Description: "",
      };

      var _Nodes = this._toObject().nodes.map((item) => {
        return {
          NodeID: item.id,
          WorkFlowID: _WorkFlowID,
          NodeType: item.type,
          NodeName: item.data.label,
          Description: item.data.label,
          Config: item,
        };
      });
      var _Edges = this._toObject().edges.map((item) => {
        return {
          EdgeID: item.id,
          WorkFlowID: _WorkFlowID,
          SourceNodeId: item.source,
          TargetNodeId: item.target,
          Condition: "",
          LName: "",
          Config: item,
        };
      });
      var _data = {
        Info: _Info,
        Nodes: _Nodes,
        Edges: _Edges,
        WorkFlowID: _WorkFlowID,
        MainID: that.workflow_data.MainID,
        MainType: that.workflow_data.MainType,
      };

      Api.subWorkFlow(_data)
        .then((res) => {
          console.log(res);
          if (res.Status) {
            that.workflow_data.WorkFlowID = res.Data;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

<template>
  <VueFlow
    ref="vueFlow"
    :nodes="nodes"
    :edges="edges"
    :class="{ dark }"
    class="basic-flow"
    :default-viewport="{ zoom: 1.5 }"
    :min-zoom="0.2"
    :max-zoom="4"
    :apply-default="false"
    fit-view-on-init
    @nodes-change="onNodesChange"
    @edges-change="onEdgesChange"
    @edgeClick="edgeClick"
    @nodeClick="nodeClick"
    :connection-line-style="connectionLineStyle"
  >
    <Background pattern-color="#f0f0f0" :gap="15" />
    <WorkFlowData
      class="vue-flow__panel top left"
      :title="workflow_data.title"
    ></WorkFlowData>
    <MiniMap position="bottom-left" class="MiniMap"></MiniMap>
    <Controls position="bottom-left">
      <ControlButton title="Reset Transform" @click="resetTransform">
        <ToolIcon name="reset" />
      </ControlButton>

      <ControlButton title="Toggle Dark Mode" @click="toggleDarkMode">
        <ToolIcon v-if="dark" name="sun" />
        <ToolIcon v-else name="moon" />
      </ControlButton>

      <ControlButton title="Log `toObject`" @click="logToObject">
        <ToolIcon name="log" />
      </ControlButton>
    </Controls>

    <Panel class="ToolBox vue-flow__panel top left vue-flow__controls">
      <ToolBox
        :is-visible="ShowNodeBox"
        :workflow-i-d="WorkFlowID"
        @addNode="addNode"
      ></ToolBox>
      <ControlButton title="Reset Transform" @click="onShowNodeBox">
        <ToolIcon v-if="!ShowNodeBox" name="zoomin" />
        <ToolIcon v-else name="zoomout" />
      </ControlButton>
    </Panel>

    <template #edge-custom="props">
      <CustomEdge
        :id="props.id"
        :source-x="props.sourceX"
        :source-y="props.sourceY"
        :target-x="props.targetX"
        :target-y="props.targetY"
        :source-position="props.sourcePosition"
        :target-position="props.targetPosition"
        :data="props.data"
        :marker-end="props.markerEnd"
        :style="props.style"
      />
    </template>

    <template #node-Start="props">
      <StartNode
        :id="props.id"
        :data="props.data"
        :isSelected="props.selected"
        @saveNode="saveNode"
      ></StartNode>
    </template>
    <template #node-End="props">
      <EndNode
        :id="props.id"
        :data="props.data"
        :isSelected="props.selected"
        @saveNode="saveNode"
      ></EndNode>
    </template>
    <template #node-LargeModel="props">
      <LargeModelNode
        :id="props.id"
        :data="props.data"
        :isSelected="props.selected"
        :largeModel="largeModelList"
        @saveNode="saveNode"
      ></LargeModelNode>
    </template>
    <template #node-KnowledgeBase="props">
      <KnowledgeBaseNode
        :id="props.id"
        :data="props.data"
        :isSelected="props.selected"
        :knowledgeBase="knowledgeBaseList"
        @saveNode="saveNode"
      ></KnowledgeBaseNode>
    </template>
    <template #node-Plugins="props">
      <PluginsNode
        :id="props.id"
        :data="props.data"
        :isSelected="props.selected"
        :plugins="pluginsList"
        @saveNode="saveNode"
      ></PluginsNode>
    </template>
    <template #node-Selector="props">
      <SelectorNode
        :id="props.id"
        :data="props.data"
        :isSelected="props.selected"
        @saveNode="saveNode"
      ></SelectorNode>
    </template>
  </VueFlow>
</template>
