<script setup>
import { Handle, Position } from "@vue-flow/core";
</script>
<script>
export default {
  name: "SelectorNode",
  props: {
    id: String,
    isSelected: Boolean,
    data: Object,
  },
  created() {},
  data() {
    return {
      label: this.data.label,
      nodeData: this.data,
      isFold: true,
      isFold_inputs: true,
      isFold_selector: true,
      selectorList: this.data.selector
        ? this.data.selector.map((item, i) => {
            item.top = i * 25 + 240;
            item.top_str = "top:" + item.top + "px";
            return item;
          })
        : [],
      comparison: [
        { name: "等于", data: "=", text: "等于" },
        { name: "不等于", data: "!=", text: "不等于" },
        { name: "大于", data: ">", text: "大于" },
        { name: "小于", data: "<", text: "小于" },
        { name: "包含", data: "~=", text: "包含" },
        { name: "不包含", data: "!~=", text: "不包含" },
      ],
    };
  },
  methods: {
    resetHandleTop() {
      var vHeight = 185;
      if (this.isFold_inputs) {
        vHeight = vHeight + 55;
      }
      if (this.isFold_selector && this.isFold) {
        if (this.isFold || this.isFold_inputs) {
          this.selectorList = this.selectorList.map((item, i) => {
            item.top = i * 25 + vHeight;
            item.top_str = "top:" + item.top + "px";
            return item;
          });
        }
      } else {
        this.selectorList = this.selectorList.map((item) => {
          item.top = 0;
          item.top_str = "";
          return item;
        });
      }
    },
    saveNode() {
      this.$emit("saveNode", {
        id: this.id,
        data: { ...this.nodeData, label: this.label, selector: this.selectorList },
      });
    },
    saveLabel() {
      this.saveNode();
    },
    checkFold() {
      this.isFold = !this.isFold;
      this.resetHandleTop();
    },
    checkFoldInputs() {
      this.isFold_inputs = !this.isFold_inputs;
      this.resetHandleTop();
    },
    checkFoldSelector() {
      this.isFold_selector = !this.isFold_selector;
      this.resetHandleTop();
    },
    onAdd() {
      var id = this.selectorList.length + 1;
      var top = this.selectorList.length * 25 + 240;
      this.selectorList.push({
        id: "Handle_" + id,
        inputs: "2",
        comparison: "",
        value: "",
        top: top,
        top_str: "top:" + top + "px",
      });
      this.saveNode();
    },
    onDel(selector) {
      this.selectorList = this.selectorList.filter((item) => item.id !== selector.id);
      this.saveNode();
    },
  },
};
</script>

<template>
  <Handle
    id="target_1"
    class="Handle"
    type="target"
    :position="Position.Left"
    :style="{}"
  />
  <div v-for="item in selectorList" :key="item.id">
    <Handle
      :id="item.id"
      class="Handle"
      type="source"
      :position="Position.Right"
      :style="item.top_str"
    />
  </div>
  <div class="NodeBox SelectorNode iconfont" :class="[isSelected ? 'Selected' : '', '']">
    <div class="Title">
      <span
        class="Icon fold"
        :class="[isFold ? 'icon-jiantou-d' : 'icon-jiantou-r']"
        @click="checkFold"
      ></span>
      <span class="Icon ticon icon-selector1"></span>
      <span>选择器</span>
      <input
        class="label"
        v-model="label"
        value="{{label}}"
        placeholder="备注"
        @blur="saveLabel"
      />
    </div>
    <div class="Content" v-if="isFold">
      <div class="Description">可以通过选择器的定义，链接多个下游执行节点</div>
      <div class="Inputs Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_inputs ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldInputs"
          ></span>
          <span>输入</span>
        </div>
        <div v-if="isFold_inputs">
          <table>
            <thead>
              <th>变量名</th>
              <th>描述</th>
            </thead>
            <tbody>
              <tr v-for="(item, i) in nodeData.inputs" :key="i">
                <td><input v-model="item.varname" /></td>
                <td><input v-model="item.txt" /></td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div class="Selector Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_selector ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldSelector"
          ></span>
          <span>条件判断</span>
        </div>
        <div v-if="isFold_selector">
          <table>
            <thead>
              <th>变量名</th>
              <th>判断条件</th>
              <th>比较值</th>
              <th>删除</th>
            </thead>
            <tbody>
              <tr v-for="item in selectorList" :key="item.id">
                <td>
                  <select v-model="item.inputs">
                    <option
                      v-for="(item, i) in nodeData.inputs"
                      :key="i"
                      :value="item.varname"
                    >
                      {{ item.varname }}
                    </option>
                  </select>
                </td>
                <td>
                  <select v-model="item.comparison">
                    <option
                      v-for="_item in comparison"
                      :key="_item.name"
                      :value="_item.data"
                    >
                      {{ _item.text }}
                    </option>
                  </select>
                </td>
                <td><input placeholder="留空代表字符串空" v-model="item.value" /></td>
                <td>
                  <span class="Icon ticon icon-del del" @click="onDel(item)"></span>
                </td>
              </tr>
            </tbody>
            <footer>
              <tr>
                <td colspan="4" class="add" @click="onAdd">
                  <span class="Icon ticon icon-add"></span>
                  <span>增加条件</span>
                </td>
              </tr>
            </footer>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>
