<script setup>
import { Handle, Position } from "@vue-flow/core";
</script>
<script>
export default {
  name: "EndNode",
  props: {
    id: String,
    isSelected: Boolean,
    data: Object,
  },
  data() {
    return {
      label: this.data.label,
      nodeData: this.data,
      isFold: true,
      isFold_outputs: true,
      isFold_prompt: true,
    };
  },
  methods: {
    saveNode() {
      this.$emit("saveNode", { id: this.id, data: { ...this.nodeData, label: this.label } });
    },
    saveLabel() {
      this.saveNode();
    },
    checkFold() {
      this.isFold = !this.isFold;
    },
    checkFoldOutputs() {
      this.isFold_outputs = !this.isFold_outputs;
    },
    checkFoldPrompt() {
      this.isFold_prompt = !this.isFold_prompt;
    },
  },
};
</script>
<template>
  <Handle class="Handle" type="target" :position="Position.Left" :style="{}" />
  <div class="NodeBox EndNode iconfont" :class="[isSelected ? 'Selected' : '', '']">
    <div class="Title">
      <span
        class="Icon fold"
        :class="[isFold ? 'icon-jiantou-d' : 'icon-jiantou-r']"
        @click="checkFold"
      ></span>
      <span class="Icon ticon icon-end"></span>
      <span>结束</span>
    </div>
    <div class="Content" v-if="isFold">
      <div class="Description">工作流的结束节点，用于返回工作流运行后的结果信息</div>
      <div class="Outputs Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_outputs ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldOutputs"
          ></span>
          <span>输出</span>
        </div>
        <div v-if="isFold_outputs">
          <table>
            <thead>
              <th>变量名</th>
              <th>描述</th>
            </thead>
            <tbody>
              <tr v-for="(item,i) in nodeData.output" :key="i">
                <td><input v-model="item.varname" /></td>
                <td><input v-model="item.txt" /></td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div class="Prompt Content">
        <div class="Title">
          <span
            class="Icon fold"
            :class="[isFold_prompt ? 'icon-jiantou-d' : 'icon-jiantou-r']"
            @click="checkFoldPrompt"
          ></span>
          <span>提示词（将输出变量与提示词整理，并让AI重新生成返回结果）</span>
        </div>
        <div v-if="isFold_prompt">
          <textarea v-model="nodeData.prompt" :placeholder="['请输入提示词：'+nodeData.output.map(item => '{{'+item.varname+'}}').join(',')]"></textarea>
        </div>
      </div>
    </div>
  </div>
</template>
